/*
 *   Name: Bizagi Workportal Desktop Project Plan Activity Users Controller
 *   Author: Elkin Fernando Siabato Cruz
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.plan.activity.users", {}, {
   /*
    *   Constructor
    */
   init: function (workportalFacade, dataService, params) {
      var self = this;

      self.usersMap = {};
      self.plugins = {};
      self.usersInformation=[];
      self.globalHandlersService = bizagi.injector.get('globalHandlersService');
      self.usersAssignees = [];

      // Call base
      self._super(workportalFacade, dataService, params);

      //Load templates
      //The load template the users plan because they are the same
      self.loadTemplates({
         "plan-users-main": bizagi.getTemplate("bizagi.workportal.desktop.project.plan.users").concat("#project-plan-users")
      });
   },

   /*
    * Renders the template defined in the widget
    */
   renderContent: function () {
      var self = this;
      self.content = $("<div></div>");
      return self.content;
   },

   /*
    * links events with handlers
    */
   postRender: function () {
      var self = this;
      self.sub("LOAD_INFO_ACTIVITY_SUMMARY", $.proxy(self.onNotifyLoadInfoActivityExecution, self));
   },

   /*
    * validate if the activity is running
    */
   activityRuning: function (activity) {
      return activity.startDate !== null && activity.finishDate === null;
   },

   onNotifyLoadInfoActivityExecution: function (event, params) {
      var self = this,
      cvsGuidUsers = [],
         activitiesUsers = [],
         $content = self.getContent().empty();
      self.params = $.extend(self.params, params.args);

      var ownerUserId = self.params.plan.idUserCreator;
      var ownerUserGuid = self.params.plan.idUserCreator;

      activitiesUsers.push({ "idUser": ownerUserId, "guidUser": ownerUserGuid, "userType": ["owner"] });
      self.usersMap["-" + ownerUserId + "-"] = {
         picture: "",
         id: ownerUserId,
         guid: ownerUserGuid,
         name: "",
         userType: ["owner"]
      };
      cvsGuidUsers.push(ownerUserGuid);
      $.each(self.params.plan.activities, function (index, activity) {
         if (self.activityRuning(activity)) {
            var userId = activity.userAssigned;
            var userGuid = activity.userAssignedGuid;
            if (userId === ownerUserId) {
               if($.inArray("Assigned", activitiesUsers[0].userType) === -1){
                  activitiesUsers[0].userType.push("Assigned");
                  self.usersMap["-" + ownerUserId + "-"].userType.push("Assigned");
               }
            }
            else {
               self.usersMap["-" + userId + "-"] = {
                  picture: "",
                  id: userId,
                  name: "",
                  userType: ["Assigned"]
               };
               if (activitiesUsers.filter(function(user) { return user.idUser == userId; }).length === 0) {
                  var id=userGuid?userGuid:userId;
                  activitiesUsers.push({"idUser": id, "userGuid": id, "userType": ["Assigned"]});
                  cvsGuidUsers.push(id);
               }
            }  
         }
      });
      self.activitiesUsers = activitiesUsers;

      //Update widget
      var contentTemplate = self.getTemplate("plan-users-main");
      contentTemplate
         .render({
            assignee: self.justAssignees(activitiesUsers),
            label: bizagi.localization.getResource("workportal-project-plan-assignee")
         })
         .appendTo($content);
        self.addEventHandlersModal();
      $.when(self.callGetDataUsers(cvsGuidUsers)).then(function () {
         self.showCreatorInformation(ownerUserId);
         self.renderUserProfilesAndImages();
      });
   },

   /**
    * return standar userAdapter information with only assigned users
    * @param {Array} allUsers 
    */
   justAssignees: function (allUsers) {
       var usersAssignee=[];
       allUsers.map(function(user) {
           var isOwner=user.userType.indexOf("owner")!==-1;
           var isAssignee=user.userType.indexOf("Assigned")!==-1;
           usersAssignee.push(usersAdapter.createJsonUserInfo(user.idUser, "", "", "", "", "", isAssignee, isOwner, [], []));
       });
       return usersAdapter.justAssignees(usersAssignee);
   },
   
   /**
    * show the information of the creator case
    * @param {string} ownerUserId guid user creator
    */
   showCreatorInformation: function (ownerUserId) {
       var self = this;
       var userCreator=self.usersMap["-"+ownerUserId+"-"];
       var userInfo = usersAdapter.createJsonUserInfo(
           userCreator.id,
           userCreator.name,
           userCreator.username,
           userCreator.picture?userCreator.picture:undefined,
           userCreator.email,
           userCreator.name.getInitials(),
           false,
           true,
           [],
           []
       );
       self.content.find(".ui-bizagi-wp-project-plan-users .ui-bizagi-wp-project-users-creator-info").userinformation(self, { user: userInfo });
   },

   /**
    * UI
    */

   renderUserProfilesAndImages: function () {
       var self = this;
       $.each(self.usersMap, function (i, obj) {
           var $li = $(".ui-bizagi-wp-userlist li[data-iduser=" + obj.id + "]", self.content);
           if (obj.picture !== "") {
               $li.find("img").prop("src", obj.picture);
           }
           else {
               $li.find("img").hide();
               if(obj.name!==undefined){
                  $li.find("span").html(obj.name.getInitials());
               }
               else{
                  console.log("obj.name is undefined");
               }

           }
       });
       $(".ui-bizagi-wp-userlist li", self.content).not(".moreUsers").on('mouseenter', function (ev) {
         var $target = $(ev.target);  
         var userId = $(this).data("iduser");
         var userSelected;
         $.each(self.usersMap, function (i, obj) {
            if(obj.id === userId)
               userSelected=obj;
        });
         var usersActivities=self.usersAssignees.find(function(assignee) {
            return assignee.userAssigned === userSelected.id;
         });
         var activities=[];
         if(usersActivities)
            usersActivities.activities.map(function(activity) {
               activities.push(activity.name);
            });
         var userParam = usersAdapter.createJsonUserInfo(userSelected.id, userSelected.name, userSelected.username, userSelected.picture, userSelected.email, userSelected.name.getInitials(), self.getIsAssignee(userSelected), self.getIsOwner(userSelected), activities, undefined);
                     
         $target.parent().usertooltip(self,{target:$target,user:userParam});
      });
   },

   getIsAssignee: function(userSelected){
      if(userSelected.userType.indexOf("Assigned") > -1){
          return true;
      }
      return false;
     },
  
     getIsOwner : function(userSelected){
      if(userSelected.userType.indexOf("owner") > -1){
          return true;
      }
      return false;
     },
   /*
    *  Get data for users
    */
   callGetDataUsers: function (cvsGuidUsers) {
      var self = this,
         params = {},
         defer = $.Deferred();
      params = {
         usersGuids: cvsGuidUsers.join(),
         width: 50,
         height: 50
      };
      $.when(self.dataService.getUsersData(params)).always(function (response) {
        self.usersInformation= response;
         for (var i = 0, length = response.length; i < length; i += 1) {
            if (typeof (response[i].name) === "undefined") {
               bizagi.log(response[i] + " Id Not Found", response, "error");
            } else {
               if(self.usersMap["-" + response[i].id + "-"]){
                  self.usersMap["-" + response[i].id + "-"].picture += (response[i].picture) ? "data:image/png;base64," + response[i].picture : "";
                  self.usersMap["-" + response[i].id + "-"].name = response[i].name || "";
                  self.usersMap["-" + response[i].id + "-"].username = response[i].username || "";
                  self.usersMap["-" + response[i].id + "-"].email = response[i].email || "";
               }
               else{
                  console.log("The object is undefined");
               }
            }
         }
         self.getUsersAssignees();
         defer.resolve();
      });
      return defer.promise();
   },

getUsersAssignees: function(){
    var self = this;
    for(var i=0; i<self.usersInformation.length; i++){
        self.usersAssignees.push(self.getUserAssignee(self.usersInformation[i]));
    }

},

getUserAssignee: function(user){
    var self =  this;
    var tasks = [];
    $.each(self.params.plan.activities, function (index, activity) {
        var userId = activity.userAssigned;
        if (userId === user.id && self.activityRuning(activity)) {
            tasks.push({ "name": activity.name });
        }
    });
    user.tasks = tasks;
    var types = self.activitiesUsers.find(function(actUser) {
        return actUser.idUser == user.id;
    });
    user.userType = types.userType;

    var userAssignees = usersAdapter.createJsonUserInfo(
        user.id, 
        user.name, 
        user.username, 
        user.picture ? "data:image/png;base64," + user.picture : undefined, 
        user.email, 
        user.name.getInitials(),
        self.getIsAssignee(user), 
        self.getIsOwner(user), 
        user.tasks, 
        []);

        return userAssignees;
   },

   addEventHandlersModal: function () {
    var self =  this;
    $(".moreUsers").click(function(event) {
        event.preventDefault();
        event.stopPropagation();
        var usersAssignees = self.usersAssignees || [];
        self.globalHandlersService.publish("showDialogWidget", {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_USERS_MODAL,
            closeVisible: true,
            data: usersAssignees,
            maximize: true,
            modalParameters: {
                title: bizagi.localization.getResource("workportal-project-users-title")+ " ("+usersAssignees.length+")",
                width: 790,
                height: 526,
                refreshInbox: false
            }
        });
     });
   }

});

bizagi.injector.register("bizagi.workportal.widgets.project.plan.activity.users", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.plan.activity.users], true);